footer {
    width: 100%;
    background: #EBEBEB;
    padding: 60px 0;
    .inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
        #footer-logo {
            width: 40%;
        }
        #footer-right {
            width: 60%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            .footer-nav-wrap {
                display: flex;
                justify-content: space-between;
            }
            .address {
                padding: 60px 0;
                text-align: right;
                address {
                    font-weight: 600;
                    font-size: 20px;
                }
                p {
                    padding: 20px 0;
                }
            }
        }
    }
}

@media all and (max-width : 1023px) and (min-width : 768px) {
    footer {
        .inner {
            display: block;
            padding: 0 20px;
            #footer-logo {
                padding-bottom: 20px;
            }
            #footer-right {
                width: 100%;
                .address {
                    text-align: left;
                    padding: 30px 0;
                }
            }
        }
    }
}

@media all and (max-width : 767px) and (min-width : 480px) {
    footer {
        .inner {
            display: block;
            padding: 0 20px;
            #footer-logo {
                padding-bottom: 20px;
            }
            #footer-right {
                width: 100%;
                .address {
                    text-align: left;
                    padding: 30px 0;
                }
            }
        }
    }
}

@media all and (max-width : 480px) {
    footer {
        .inner {
            display: block;
            padding: 0 20px;
            #footer-logo {
                padding-bottom: 20px;
            }
            #footer-right {
                width: 100%;
                .footer-nav-wrap {
                    display: block;
                    li {
                        padding: 3px 0;
                    }
                }
                .address {
                    text-align: left;
                    padding: 30px 0;
                }
            }
        }
    }
}