header {
    padding: 30px 0;
    position: relative;
    z-index: 20;
    &#header-wrap {
        display: flex;
        justify-content: center;
        #logo {
            width: 35%;
        }
        #m-nav-div {
            display: none;
            span {
                display: block;
                width: 35px;
                height: 3px;
                background: #333;
                margin: 4px 0;
            }
        }
        nav {
            width: 60%;
            cursor: pointer;
            #lang {
                display: flex;
                justify-content: flex-end;
                p {
                    text-align: right;
                    height: 100%;
                    position: relative;
                    &.lang-tit {
                        color: #888;
                    }
                    &:hover {
                        li {
                            display: block;
                        }
                    }
                }
                ul {
                    text-align: center;
                    transition: 0.5s;
                    position: absolute;
                    left: -10px;
                    top: 22px;
                    z-index: 20;
                    color: #333;
                    li {
                        padding: 0 10px;
                        background: #ebebeb;
                        border-bottom: 1px solid #ccc;
                        display: none;
                    }
                }
            }
            #main-nav {
                display: flex;
                justify-content: space-between;
                align-items: center;
                text-align: center;
                margin-top: 5px;
                width: 100%;
                >li {
                    position: relative;
                    width: 16.66%;
                    height: 30px;
                    &.nav-tit {
                        font-size: 20px;
                        font-weight: 600;
                    }
                    &:hover {
                        #sub-nav {
                            display: block;
                        }
                    }
                    #sub-nav {
                        width: 100%;
                        font-weight: normal;
                        font-size: 16px;
                        position: absolute;
                        left: 0;
                        top: 32px;
                        background: #fff;
                        box-shadow: 2px 2px 8px #666;
                        display: none;
                        transition: 0.5s;
                        li {
                            padding: 10px;
                        }
                    }
                }
            }
        }
    }
}

@media all and (max-width : 1023px) and (min-width : 768px) {
    header {
        &#header-wrap {
            justify-content: space-between;
            #logo {
                padding-left: 30px;
            }
            #m-nav-div {
                display: block;
                padding-right: 30px;
                &.on {
                    #m-nav {
                        right: 0;
                    }
                }
                #m-nav-icon {
                    cursor: pointer;
                }
                #m-nav-icon.on {
                    #m-nav {
                        right: 0;
                    }
                }
                #m-nav {
                    width: 30%;
                    height: 100vh;
                    position: fixed;
                    z-index: 150;
                    right: -100%;
                    top: 65px;
                    background: #ebebeb;
                    transition: 1s;
                    
                    #lang {
                        ul {
                            display: flex;
                            text-align: center;
                            justify-content: center;
                            li {
                                padding: 5px 8px;
                            }
                        }
                    }
                    #main-nav {
                        justify-content: flex-end;
                        flex-direction: column;
                        align-items: flex-end;
                        margin-top: 0;
                        padding: 10px 0;
                        color: #333;
                        transition: 0.5s;
                        border-top: 1px solid #ccc;
                        >li {
                            width: 100%;
                            text-align: center;
                            cursor: pointer;
                            transition: 0.5s;
                            &.nav-tit {
                                font-size: 20px;
                                font-weight: 600;
                                transition: 0.5s;
                            }
                            &.on {
                                #sub-nav {
                                    display: block;
                                }
                            }
                            #sub-nav {
                                font-weight: normal;
                                font-size: 16px;
                                display: none;
                                transition: 0.5s;
                                margin: 10px 0;
                                background-color: #fff;
                                li {
                                    padding: 10px 0;
                                    border-bottom: 1px solid #ccc;
                                }
                            }
                        }
                    }
                }
            }
            nav {
                display: none;
            }
        }
    }
}

