#visual {
    width: 100%;
    height: 100vh;
    .visual-img {
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        img {
            width: 100%;
            height: 100%;
        }
    }
    .time-info {
        position: relative;
        display: flex;
        background: rgba(255, 255, 255, 0.7);
        top: 40%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 50%;
        max-width: 800px;
        height: 13%;
        align-items: center;
        justify-content: center;
        h3 {
            font-weight: 600;
        }
        div {
            display: flex;
            align-items: center;
            p {
                padding: 0 15px;
                &.info-tit {
                    font-weight: 500;
                }
            }
        }
    }
    .main-service {
        position: relative;
        top: 38%;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        li {
            width: 200px;
            height: 200px;
            background: #fff;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 0 20px;
            transition: 0.5s;
            &:hover {
                background: #2C384B;
                color: #fff;
            }
            .service {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                svg {
                    font-size: 50px;
                }
                p {
                    font-size: 20px;
                    font-weight: 600;
                }
            }
        }
    }
}

@media all and (max-width : 1023px) and (min-width : 768px) {
    #visual {
        .time-info {
            flex-direction: column;
            height: 25%;
            top: 30%;
            h3 {
                padding-bottom: 10px;
                text-align: center;
                font-size: 20px;
            }
        }
        .main-service {
            top: 20%;
            li {
                width: 150px;
                height: 150px;
                .service {
                    svg {
                        font-size: 40px;
                    }
                    p {
                        font-size: 18px;
                    }
                }
            }
        }
    }
}

