#sns {
  background-color: #ebebeb;
  padding: 60px 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  text-align: center;
  h2 {
    font-size: 25px;
    font-weight: 600;
    padding: 30px 0;
  }
  .sns-video {
    width: 70%;
    height: 70%;
    img {
      width: 100%;
    }
  }
  #sns-list {
    padding: 40px 0;
    display: flex;
    width: 65%;
    margin: 0 auto;
    align-items: center;
    flex-wrap: wrap;
    li {
      width: 33.3333%;
      padding: 20px 0px;
      a {
        display: flex;
      }
      p {
        padding-left: 25px;
      }
    }
  }
}

@media all and (max-width : 480px) {
  #sns {
    .sns-video {
      width: 85%;
    }
    #sns-list {
      width: 80%;
      li {
        width: 50%;
        font-size: 14px;
        padding: 15px 15px 0 15px;
        p {
          padding-left: 10px;
        }
      }
    }
  }
}